const {ccclass, property} = cc._decorator;

@ccclass
export default class HomePlayerItem extends cc.Component {

    @property(cc.Sprite)
    private avatar: cc.Sprite = null;

    @property(cc.Label)
    private lblName: cc.Label = null;

    @property(cc.Node)
    private btnPlayWithFriend: cc.Button = null;


    onLoad() {
        // this.pathNode.zIndex = - 1;
    }

    updateItem(name: string, avatar: string): void {
        let that = this;
        this.lblName.string = name;

        cc.loader.loadRes("levelImage/" + (levelId), cc.SpriteFrame, function (err, spriteFrame) {
            that.spSource.spriteFrame = spriteFrame;
        });


    }

}
